import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'

function Form() {
    const [fname,setFname] = useState('')
    const [lname,setLname] = useState('')
    const [email,setEmail] = useState('')
    const [number,setNumber] = useState('')
    const [city,setCity] = useState('')
    const [show,setShow] = useState(false)

    const change= (e) => {
        setFname(e.target.value)
        setLname(e.target.value)
        setEmail(e.target.value)
        setNumber(e.target.value)
        setCity(e.target.value)
    }
    const FormHandler = (e) => {
            e.preventDefault();
            if(fname.length == "" && lname.length == "" && email.length == ""  && number.length == "" && city.length == "") {
                setShow(true)
                return;
            }
    }
    const [t , i18n] = useTranslation()
  return (
    <>
        <form  onSubmit={FormHandler}>
             <div className="left-box">
                        <input className='text-end' type="text" name="" id="" placeholder='First name' onChange={change} />
                        {show && <p className='text-danger text-end px-2'>*First Name Required</p>}
                        <input  className='text-end'type="text" name="" id="" placeholder='Last name' onChange={change} />
                        {show && <p className='text-danger text-end  px-2'>*Last Name Required</p>}
                        <input className='text-end' type="email" name="" id="" placeholder='Email' onChange={change}  />
                        {show && <p className='text-danger text-end px-2'>*Email Required</p>}
                        <input className='text-end' type="number" name="" id="" placeholder='Phone Numbers' onChange={change}  />
                        {show && <p className='text-danger text-end px-2'>*Number Required</p>}
                        <input className='text-end' type="text" name="" id="" placeholder='Saudi Arabia' disabled />
                        <div className="select-group one-select" onChange={change}>
                                <select name="" id="" placeholder='City'>
                                    <option className='text-end' value="" disabled selected>City</option>
                                    <option className='text-end' value="">Abha</option>
                                    <option className='text-end' value="">Al Bukayriyah</option>
                                    <option className='text-end' value="">Baljurashi</option>
                                    <option className='text-end' value="">Bisha</option>
                                    <option className='text-end' value="">Buraydah</option>
                                    <option className='text-end' value="">Al Bahah</option>
                                    <option className='text-end' value="">Dammam</option>
                                    <option className='text-end' value="">Dhahran</option>
                                    <option className='text-end' value="">Diriyah</option>
                                    <option className='text-end' value="">Dawadmi</option>
                                    <option className='text-end' value="">Al Gwei'iyyah</option>
                                    <option className='text-end' value="">Hautat Sudair</option>
                                    <option className='text-end' value="">Al Hareeq</option>
                                    <option className='text-end' value="">Hotat Bani Tamin</option>
                                    <option className='text-end' value="">Hofuf</option>
                                    <option className='text-end' value="">Hafr Al Batin</option>
                                    <option className='text-end' value="">Al Jawf</option>
                                    <option className='text-end' value="">Jeddah</option>
                                    <option className='text-end' value="">Jizan</option>
                                    <option className='text-end' value="">Jubail</option>
                                    <option className='text-end' value="">Khafji</option>
                                    <option className='text-end' value="">Khamis Mushayt</option>
                                    <option className='text-end' value="">Khobar</option>
                                    <option className='text-end' value="">Al Majma'ah</option>
                                    <option className='text-end' value="">Al Mubarraz</option>
                                    <option className='text-end' value="">Mecca</option>
                                    <option className='text-end' value="">Medina</option>
                                    <option className='text-end' value="">Muzahmiyya</option>
                                    <option className='text-end' value="">Najran</option>
                                    <option className='text-end' value="">Al Namas</option>
                                    <option className='text-end' value="">Al Qunfudhah</option>
                                    <option className='text-end' value="">Ar Rass</option>
                                    <option className='text-end' value="">Ras Tanura</option>
                                    <option className='text-end' value="">Riyadh</option>
                                    <option className='text-end' value="">Sakakah</option>
                                    <option className='text-end' value="">Shaqraa</option>
                                    <option className='text-end' value="">Taif</option>
                                    <option className='text-end' value="">Tabuk</option>
                                    <option className='text-end' value="">Tanomah</option>
                                    <option className='text-end' value="">Al `ula</option>
                                    <option className='text-end' value="">Unaizah</option>
                                    <option className='text-end' value="">Uyan Aljuwa</option>
                                    <option className='text-end' value="">Wadi Al Dawasir</option>
                                    <option className='text-end' value="">Yanbu</option>
                                    <option className='text-end' value="">Zulfi</option>
                                    

                                </select>
                        </div>
                        {show && <p className='text-danger text-end px-2'>*City Required</p>}
                        <textarea className='text-end' name="" id="" cols="1" rows="6" placeholder='Your Feedback'></textarea>
                        <button>Send</button>
             </div>
        </form>
    </>
  )
}

export default Form