import React from 'react'
import { Swiper, SwiperSlide } from "swiper/react";
import  sliderone from '../../../assets/images/slider_two.jpeg'
import  slidertwo from '../../../assets/images/slider_two.jpeg'
import  sliderthree from '../../../assets/images/slider_three.jpeg'
import  sliderfour from '../../../assets/images/slider_four.jpeg'
import  sliderfive from '../../../assets/images/slider_five.jpeg'
import  slidersex from '../../../assets/images/slider_sex.jpeg'
import { useParams } from 'react-router-dom';
import "swiper/css";
import axios from 'axios';
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Autoplay, Pagination, Navigation } from "swiper";
import '../../../assets/style/clients/Slider.css'


function SliderOne({cust}) {

  return (
    <Swiper
    autoplay={{
        delay:  1700,
    }}
    loop={true}
    modules={[Autoplay, Pagination, Navigation]}
    // spaceBetween={15}
    slidesPerView={1}
    pagination={{
        clickable: true,
      }}
      navigation={true}


>
        



            

             
              <SwiperSlide  className='slider_img' >
                <img className='mw-100' src={cust.image} alt="" />
              </SwiperSlide>

            



        
  
</Swiper>
  )
}

export default SliderOne